import {
  GET_ITEMS,
  ADD_ITEM,
  DELETE_ITEM,
  ITEMS_LOADING,
  PUT_ITEM,
  INDIVIDUAL_ITEMS
} from '../actions/types';

const initialState = {
  items: [],
  singleShipment: {},

  loading: false
};

export default function(state = initialState, action) {
  switch (action.type) {
      case GET_ITEMS:
      return {
        ...state,
        items: action.payload,
        loading: false
      };

      case INDIVIDUAL_ITEMS:
      return {
        ...state,
        singleShipment: action.payload,
        loading: false
      };

      case DELETE_ITEM:
      return {
        ...state,
        items: state.items.filter(item => item._id !== action.payload)
      };
    
      case PUT_ITEM:
      return {
        ...state,
        items: state.items.filter(item => item._id !== action.payload),
        loading: false
      };

      case ADD_ITEM:
      return {
        ...state,
        items: [action.payload, ...state.items]
      };

      case ITEMS_LOADING:
      return {
        ...state,
        loading: true
      };
      default:
      return state;
  }
}



